<template>
<div :key='refresh' :style="{height}">
      <div id="jsmind_container"></div>
  
  <div style="display:none">
      <input class="file" type="file" id="image-chooser" accept="image/*"/>
    </div>
</div>
</template>

<script>
import  "./jsmind.js";
import "./jsmind.css";
import './jsmind.draggable.js'
//import './jsmind.screenshot.js'
export default {
  name: "jsMind",
  props: {
    values: {
      type: Object,
      required: true
    },
    options: {
      type: Object
    },
    height:{
      type:String,
      required:true
    }
  },
  mounted() {
    this.init()
  },
  data(){
    return{
      jm:{},
      refresh:1,
      value:{},
       // 
      default_options : {
      container: "jsmind_container",
      editable: true, // 
      theme: "primary", // 
      mode: "full", // 
      support_html: true, // HTML
      view: {
        engine: "canvas", // 
        hmargin: 100, // 
        vmargin: 50, // 
        line_width: 2, // 
        line_color: "#555" // 
      },
      layout: {
        hspace: 30, // 
        vspace: 20, // 
        pspace: 13 // /
      },
      shortcut: {
        enable: true, // 
        handles: {
          SavePNG(){
            this.jm.screenshot.shootDownload();
          }
        }, // 
        mapping: {
          // 
          addchild: 45, // <Insert>
          addbrother: 13, // <Enter>
          editnode: 113, // <F2>
          delnode: 46, // <Delete>
          toggle: 32, // <Space>
          left: 37, // <Left>
          up: 38, // <Up>
          right: 39, // <Right>
          down: 40 ,// <Down>
          SavePNG:jsMind.key.ctrl + 83
        }
      }
    }
    }
  },
  updated(){
    this.init()
  },
  watch:{

    options(){
     this.$nextTick(()=>{
      this.refresh++
     })
    }
  },
  methods:{
    init(){
      if(this.jm.get_data){
        this.value=this.jm.get_data()
      }else{
        this.value=this.values
      }
      const options = Object.assign(this.default_options, this.options);
      this.jm=window.jsMind.show(options,this.value)
    }
  }
};
</script>
<style scoped>
#jsmind_container {
  height: 100%;
  width: 100%;
  border : 1px;
  border-style:solid;

}
</style>
